












import Vue from 'vue'

import { docsMixin } from '../../../mixins'

export default Vue.extend({
  mixins: [docsMixin],
  computed: {
    isRouterLink(): boolean {
      return this.fullTypeName.indexOf('Edm.') !== 0
    },
    isCollection(): boolean {
      return this.fullTypeName.indexOf('Collection(') === 0
    }
  },
  props: {
    fullTypeName: String,
    entityFullName: String,
    docLinkType: Number
  }
})



